body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2, h3 {
    font-size: 1.5em;
    margin: 0;
}

.btn {
    font-size: 1em;
    width: 100%;
    padding: .25em 1em;
    color: white;
    border: 1px solid #999;
    background-color: #0078e7;
    border: transparent;
    text-decoration: none;
    border-radius: 2px;
}

.btn-small {
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    border-radius: 2em;
    padding: .25em .5em;
}

.btn-float {
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

button:active {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

label {
    display: block;
    margin-top: 1.5em;
}

input {
    font-size: 1em;
    padding: 12px 20px;
    border: 1px solid #ccc;
    margin-top: .5em;
    width: calc(100% - 2em);
    border-radius: 2px;
}

.App {
    background-color: #002b36;
    min-height: 100vh;
    color: white;
}

nav {
    display: -webkit-flex;
    display: flex;
    /* justify-content: space-around; */
}

nav > * {
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: center;
    color: white;
    background-color: grey;
    padding: .5em;
    text-decoration: none;
    cursor: pointer;
}

nav > *:not(:last-child) {
    border-right: 1px solid dimgray;
}

nav > *.active {
    background-color: seagreen;
}

main {
    padding: 1em;
}

.movement-datafield {
    display: -webkit-flex;
    display: flex;
    padding: .2em 0;
    color: rgba(255, 255, 255, .5);
}

.movement-datafield.active {
    color: white;
}

.movement-datafield > :first-child {
    width: 5.5em;
}

.movement-datafield > :nth-child(2) {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.switch {
  position: relative;
  display: inline-block;
  width: 42px;
  height: 24px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
  background-color: red;
}

label {
    margin: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
  border-radius: 24px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(18px);
  transform: translateX(18px);
}

.traffic-data {
    font-size: 1.5em;
    background-color: #fdf6e3;
    padding: 1em;
    color: black;
    margin-bottom: 1em;
}

.data-box {
    margin-bottom: .5em;
}

#positions {
    background-color: lightgrey;
    padding: .5em;
}

.position {
    background-color: white;
    border-radius: .5em;
    padding: .5em;
}

.modal-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.modal-content {
    font-size: 1.25em;
}

.map-nav {
    position: absolute;
    top: 0;
    left: 0;
    padding: 1em;
}

.position-marker {
    color: red;
}

.map-marker {
    border-radius: 2em;
    background-color: navy;
    color: white;
    font-weight: bolder;
    padding: .25em .5em;
}

.pie-wrapper {
    height: 40vh;
    position: relative;
}

.readouts {
    text-align: center;
    position: absolute;
    bottom: 0;
    width: 100%;
}

